.menu {
  margin: 0;
  padding: 0;
  -webkit-box-shadow: 0px 0px 5px rgba(9, 9, 8, 0.73);
          box-shadow: 0px 0px 5px rgba(9, 9, 8, 0.73);
  background-color: white;
}

.menu ul {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}

.menu__item {
  padding: 15px;
  font-size: 18px;
  font-family: 'Roboto', sans-serif;
  cursor: pointer;
  border-bottom: 2px solid white;
  -moz-user-select: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  opacity: 1;
}

.menu__item:hover {
  color: #f22;
  border-bottom: 2px solid #f22;
}

.active,
.active:hover {
  color: #f22;
  border-bottom: 2px solid #f22;
}

.fixed {
  width: 100%;
  position: fixed;
  opacity: 0.8;
  top: 0;
  left: 0;
}

@media (max-width: 800px) {
  .menu__item {
    font-size: 18px;
  }
}

@media (max-width: 550px) {
  .menu__item {
    font-size: 15px;
  }
}

@media (max-width: 320px) {
  .menu__item {
    font-size: 13px;
  }
}



